<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo pp:</h4>

		<b-form>
			<b-form-group label="*Cultivo"
						  label-for="id_cultivo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_cultivo"
								v-model="pp.id_cultivo"
								:items="opcultivos"
								placeholder="Elija el cultivo"
								:state="validacion.id_cultivo.estado"
								invalid-feedback="Complete este campo"
								required
								solo
								filled>
				</v-autocomplete>
				<b-form-invalid-feedback id="id_cultivo-live-feedback">
					{{ validacion.id_cultivo.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<!-- nro_ticket -->
			<b-form-group label="*Fecha"
						  label-for="fecha"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="fecha"
							  v-model="pp.fecha"
							  type="date"
							  placeholder="Ingrese el numero del patente"
							  :state="validacion.fecha.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="fecha-live-feedback">
					{{ validacion.fecha.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Pp"
						  label-for="pp"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="pp"
							  v-model="pp.pp"
							  type="number"
							  placeholder="Ingrese el numero de pp"
							  :state="validacion.pp.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="pp-live-feedback">
					{{ validacion.pp.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			class="mt-2"
			variant="success"
			block
			@click="postPp()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				pp: {},
				data: {},
				opcultivos: [],
				validacion: {
                    id_cultivo: { estado: null, mensaje: "" },
					fecha: { estado: null, mensaje: "" },
                    pp: { estado: null, mensaje: "" },
				},
				respuesta: {},
			};
		},
		methods: {
			async getPp() {
                let PpAPI = new APIControler();
                this.data = await PpAPI.getData();
			},
            async getCultivos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "cultivos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/cultivos/" + element.id_cultivo + "/";
                    option.text = element.id_campo + element.tipo + element.coordenadas;
                    this.opcultivos.push(option);
                });
            },
			async postPp() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "pp/";
				this.respuesta = await gastoSalienteAPI.postData(this.pp);
				this.cargarFeedback();
				this.updateTable();
			},
            fechaactual() {
                const f = new Date();
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
            inicializar() {
                this.pp.fecha = this.fechaactual();
            },
		},
        beforeMount() {
			this.inicializar();
			this.getCultivos();
        },
	};
</script>

<style></style>
