<template>
	<v-app id="app">
		<div id="pp" class="myTable">
			<vue-headful title="Pp - Aranda Distribucion"></vue-headful>
			<b-form-group label="*Campo"
						  label-for="id_campo"
						  @submit.stop.prevent="handleSubmit">
				<v-autocomplete id="id_campo"
								v-model="campousado"
								@change="cambiofecha()"
								:items="opcampos"
								placeholder="Elija el campo"
								required
								solo
								filled>
				</v-autocomplete>
			</b-form-group>
			<h5 v-if="campousado != null">Lotes del campo:</h5>
			<div v-for="item in this.getLotes(getCampo(this.campousado))">
				<p>Tipo: {{item.tipo}} - Tipo Suelo: {{item.tiposuelo}}</p>
			</div>
			<b-form-group label="Fecha Inicial" label-for="fechainiusada" style="display:inline-block;width:50%;">
				<b-form-input id="fechainiusada"
							  v-model="fechainiusada"
							  @change="cambiofecha()"
							  type="date"
							  placeholder="Ingrese la fecha en la que quiere el reporte"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
			</b-form-group>
			<b-form-group label="Fecha Final" label-for="fechafinusada" style="display:inline-block;width:50%;">
				<b-form-input id="fechafinusada"
							  v-model="fechafinusada"
							  type="date"
							  @change="cambiofecha()"
							  placeholder="Ingrese la fecha en la que quiere el reporte"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
			</b-form-group>
			<h2>Listado de Pp</h2>
			<b-button @click="testFetch"
					  class="mb-4"
					  title="Recargar"
					  variant="light">
				<v-icon dark style="color: black">mdi-cached</v-icon>
				Actualizar
			</b-button>
			<b-button class="mb-4 ml-2"
					  variant="success"
					  id="btn_del_full"
					  title="Importar desde Excel"
					  style="color: white"
					  v-if="dardealta == false"
					  v-b-modal.modal-excel>
				Importar Excel
			</b-button>
			<b-modal no-close-on-backdrop
					 ref="my-modal"
					 id="modal-excel"
					 hide-footer
					 title="Importar desde Excel"
					 ok-only>
				<h4>Elige un archivo para importar</h4>
				<p>Recuerda que un link para id_campo se forma de la siguiente manera:</p>
				<b>http://45.162.168.170:8081/campos/ + id_campo (puedes encontrarlo en el listado de campos)</b>
				<br />
				<br />
				<input type="file" @change="uploadFile" ref="archivo" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" />
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
					Volver Atras
				</b-button>

				<b-button class="mt-3"
						  variant="success"
						  block
						  title="Importar"
						  @click="importarExcel">
					Importar
				</b-button>
			</b-modal>
			<!-- ================ALTA GASTO SALIENTE======================== -->
			<b-button class="mb-4 ml-2"
					  v-b-modal.modal-alta
					  @click="altaPp()"
					  title="Nuevo Pp"
					  v-if="dardealta == false"
					  style="color: white">
				<v-icon dark> mdi-plus </v-icon>
				Nuevo Pp
			</b-button>
			<b-modal id="modal-alta" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Alta</h5>
				</template>
				<pp-alta :updateTable="testFetch" />
			</b-modal>

			<!-- ================ELIMINAR VARIOS GASTOS======================== -->
			<div>
				<b-modal ref="my-modal"
						 id="modal-eliminarTodo"
						 hide-footer
						 title="Eliminar"
						 ok-only>
					<div class="d-block text-center" v-if="selected.length === rows">
						<h3>¿Esta seguro de eliminar todos los registros ?</h3>
					</div>
					<div class="d-block text-center" v-else>
						<h3>¿Esta seguro de eliminar {{ selected.length }} registros ?</h3>
					</div>

					<b-button class="mt-2" block @click="hideModal" title="Volver Atras">
						Volver Atras
					</b-button>

					<b-button class="mt-3"
							  variant="danger"
							  block
							  title="Eliminar"
							  @click="delete_all_Pp()">
						Eliminar
					</b-button>
				</b-modal>
			</div>
			<!-- ======================================== -->
			<!-- ======== Formulario de Busqueda ======== -->
			<b-form-group label-for="filter-input"
						  label-align-sm="right"
						  label-size="sm"
						  class="mb-0"
						  style="width: 100%; padding-bottom: 1em"
						  v-show="rows > 0">
				<b-input-group size="sm">
					<b-form-input id="filter-input"
								  v-model="filter"
								  type="search"
								  placeholder="Buscar registros"></b-form-input>

					<b-input-group-append>
						<b-button :disabled="!filter" @click="filter = ''">Limpiar</b-button>
					</b-input-group-append>
				</b-input-group>
			</b-form-group>
			<!-- ======================================== -->
			<div>
				<pre>Cantidad de registros: {{ rows }}</pre>
			</div>
			<div v-for="(item, index) in periodocomplpp" :key="index">
				<h4>Mes: {{item.mesusado}}</h4>
				<b-table :fields="llenarfields(item.tabla_fechas)"
						 striped
						 sortable
						 responsive
						 hover
						 :items="item.tabla_pp"
						 show-empty
						 :per-page="perPage"
						 :current-page="currentPage"
						 :sticky-header="false"
						 :no-border-collapse="true"
						 ref="tablaregistros"
						 id="tablaregistros"
						 :filter="filter"
						 @filtered="onFiltered">
					<template #empty="">
						<b>No hay registros para mostrar</b>
					</template>

					<template slot="cell()" slot-scope="data" style="min-width: 4em !important; margin-left: 2em !important;">
						<p @click="showinput()" v-if="ppus === true">
							<v-icon class="mr-1" style="font-size:1.2em;display:inline-block" v-if="dardealta == false"> mdi-pencil </v-icon>
							<span style="display:inline-block;">{{ data.value }}</span>
						</p>
						<b-form-group label-for="precioref" v-else>
							<b-form-input id="precioref"
										  v-model="data.value"
										  type="number"
										  style="width:100%;display:inline-block;"
										  invalid-feedback="Complete este campo">
							</b-form-input>
							<b-button @click="GuardarPrecioRef(data)" style="background-color:green;display:inline-block;">✔</b-button>
						</b-form-group>
					</template>
					<template slot="cell(fecha)" slot-scope="data">
						{{ data.value | Date}}
					</template>

					<template #cell(selected)="{ rowSelected }">
						<template v-if="rowSelected">
							<span aria-hidden="true">&check;</span>
							<span class="sr-only">Selected</span>
						</template>
						<template v-else>
							<span aria-hidden="true">&nbsp;</span>
							<span class="sr-only">Not selected</span>
						</template>
					</template>
					<template #row-details="row">
						<b-card title="Datos del pp: ">
							<div>
								<b-list-group horizontal>
									<b-list-group class="col-3">
										<b-list-group-item><b>Id pp:</b> {{ row.item.id_pp }}</b-list-group-item>
										<b-list-group-item>
											<b>Numero de ticket:</b>
											{{ row.item.nro_ticket }}
										</b-list-group-item>
										<b-list-group-item>
											<b>Descripcion:</b>
											{{ row.item.descripcion }}
										</b-list-group-item>
										<b-list-group-item><b>Total:</b> {{ row.item.total }}</b-list-group-item>
										<b-list-group-item><b>Fecha:</b> {{ row.item.fecha }}</b-list-group-item>
									</b-list-group>
									&nbsp;
								</b-list-group>
							</div>
						</b-card>
					</template>
				</b-table>
			</div>
			<!-- ======== Tabla con los registros ======= -->
			<!-- ================ELIMINAR GASTO SALIENTE======================== -->

			<b-modal id="modal_eliminar"
					 ref="my-modal"
					 hide-footer
					 title="Eliminar"
					 ok-only>
				<div class="d-block text-center">
					<h3>
						¿Esta seguro de eliminar los datos de
						{{ infoEliminar.pp.id_pp }} - Nro ticket
						{{ infoEliminar.pp.patente }}?
					</h3>
				</div>
				<b-button class="mt-2" block @click="hideModal" title="Volver Atras">Volver Atras</b-button>
				<b-button class="mt-3"
						  variant="danger"
						  block
						  @click="deletePp(infoEliminar.pp.id_pp)"
						  title="Eliminar">
					Eliminar
				</b-button>
			</b-modal>
			<b-container fluid>
				<b-col class="my-1">
					<b-pagination v-model="currentPage"
								  align="center"
								  pills
								  :total-rows="rows"
								  :per-page="perPage"
								  aria-controls="table_pp">
					</b-pagination>
				</b-col>
			</b-container>
			<b-modal id="modal-editar" hide-footer>
				<template #modal-title>
					<h5 class="modal-title">Editar</h5>
				</template>
				<pp-update :pp="editar" :updateTable="testFetch" />
			</b-modal>
		</div>
	</v-app>
</template>

<script>
    let api = new URL("http://45.162.168.170:8081");
	api.pathname = "pp";
	//api.port = 8081;
    import { APIControler } from "@/store/APIControler";
	import PpAlta from "./PpAlta.vue";
	import PpUpdate from "./PpUpdate.vue";
    const XLSX = require("xlsx");
	import axios from "axios";

	export default {
        components: { PpAlta, PpUpdate },
		data() {
			return {
				tabla_pp: [],
				fields: [
                    { key: "mes", label: "Mes", sortable: true },
				],
				selected: [],
				periodocomplpp: [],
				fechas: [],
				pps: [],
                dardealta: false,
				cultivousado: null,
				lotescampo: [],
				campousado: null,
				cultivos: [],
				filter: null,
				totalRows: 1, //Total de filas
				currentPage: 1, //Pagina actual
				perPage: 10, // Datos en la tabla por pagina
				buscar: "",
                opcampos: [
                    { value: null, text: "Elija un campo", disabled: true },
                ],
				editar: {},
				fechainiusada: null,
				ppus: true,
				fechafinusada: null,
				infoEliminar: {
					id: "modal_eliminar",
					pp: -1,
				},
				btn_down_pdf: true, //Desabilito los botones, hasta que muestre los datos
				btn_del_full: true,
				msj_tabla: " Presione 'Mostrar' para ver los regitros ",
				btn_mostrar: false,
				btn_editar: false,
				btn_eliminar: false,
				btn_select: false,
				btn_limpiar: true,
			};
		},
		computed: {
			rows() {
                return (this.totalRows = this.tabla_pp.length);
			},
			rowsFilter() {
				return this.totalRows;
			},
			id() {
                return this.tabla_pp.id_pp;
			},
			sortOptions() {
				// Create an options list from our fields
				return this.fields
					.filter((f) => f.sortable)
					.map((f) => {
						return { text: f.label, value: f.key };
					});
			},
		},
		methods: {
			cambiofecha()
			{
                this.periodocompletardo();
			},
			GuardarPrecioRef(data)
			{
				let fechaza = data.field.key
                fechaza = fechaza.split('-')[2] + '-' + fechaza.split('-')[1] + '-' + fechaza.split('-')[0]
				if (this.findppfecha(fechaza) == null) {
					let pp = {
						fecha: fechaza,
						pp: data.value,
                        id_campo: this.campousado,
					};
					this.postPp(pp);
				} else
				{
                    let pp = this.findppfecha2(fechaza)
					pp.pp = data.value;
                    this.putPp(pp);
				}
				console.log(data)
				this.ppus = true;
			},
            async postPp(pp) {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "pp/";
				this.respuesta = await gastoSalienteAPI.postData(pp);
                //this.actualizarbh(pp.fecha)
			},
            uploadFile() {
                this.images = this.$refs.archivo.files[0];
                console.log(this.images);
            },
            async importarExcel() {
                const data = await this.images.arrayBuffer();
                let workbook = XLSX.read(data);
                var sheet_name_list = workbook.SheetNames;
                console.log('workbook1');
                console.log(workbook);
                console.log('SheetNames');
                console.log(workbook.SheetNames);
                let ProveedorAPI = new APIControler();
                let datos = XLSX.utils.sheet_to_json(workbook.Sheets[sheet_name_list[0]]);
                console.log("datos");
                console.log(datos);
                ProveedorAPI.apiUrl.pathname = "pp/"
                datos.forEach((element) => {
                    ProveedorAPI.postData(element)
                });
                this.testFetch();
            },
            async putPp(pp) {
                let respuesta = "vacio";
                await axios
                    .put(
                        "http://45.162.168.170:8081/pp/" +
                        pp.id_pp +
                        "/",
                        pp
                    )
                    .then(function (data) {
						swal("Operación Exitosa", " ", "success");
                        //this.actualizarbh(pp.fecha)
                    })
                    .catch(function (error) {
                        const mje =
                            error.response.status < 500
                                ? "Los datos no son válidos"
                                : "Se ha detectado un problema ";
                        swal("¡ERROR!", mje, "error");
                        respuesta = error.response.data;
                    });
            },
			async testFetch() {
				try {
					const res = await fetch(api);
					const data = await res.json();

					var lista_pp = data.results;

					this.periodocomplpp.forEach((opcion) =>
					{
						opcion.tabla_pp = [];
						let fila = {};
						opcion.tabla_fechas.forEach((fecha) =>
						{
                            let fechuca = fecha.split('-')[2] + '-' + fecha.split('-')[1] + '-' + fecha.split('-')[0]
							let ppfecha = this.findppfecha(fechuca)
							fila[fecha] = ppfecha;
						})
						console.log(fila);
                        opcion.tabla_pp.push(fila);
                        console.log('Tabla gooooooood');
						console.log(opcion.tabla_pp);
					})
				} catch (error) {
					console.log(error);
				}
			},
			editarPp(item, index) {
				this.editar = item;
			},
			//Funcion para mostrar el modal
			showModal() {
				this.$refs["my-modal"].show();
			},
			showModalinfo(item, index) {
				this.infoEliminar.pp = item;
				this.showModal();
			},
			//Funcion para esconder el modal
			hideModal() {
				this.$refs["my-modal"].hide();
			},
            fechaperiodo() {
                this.fechas = [];
				let fechaini = new Date(this.fechainiusada)
                fechaini.setDate(fechaini.getDate() + 1)
				let fechafin = new Date(this.fechafinusada)
                fechafin.setDate(fechafin.getDate() + 1)
                console.log(fechaini.getDay());
                while (fechaini.getTime() <= fechafin.getTime()) {
                    this.fechas.push(('0' + fechaini.getDate()).slice(-2) + "-" + ('0' + (fechaini.getMonth() + 1)).slice(-2) + "-" + fechaini.getFullYear());
                    fechaini.setDate(fechaini.getDate() + 1);
                }
                console.log(this.fechas);
			},
			async getPps()
			{
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "pp/";
                this.pps = await gastoSalienteAPI.getData();
			},
			findppfecha(fecharda)
			{
				console.log(this.pps);
                let ppfecha = this.pps.find((s) => {
                    return (s.fecha == fecharda && s.id_campo == this.campousado);
				})
				if (ppfecha == undefined) {
					return null
				} else
				{
					return ppfecha.pp;
				}
			},
            actualizarbh(fechaza) {
                let eto = findetofecha(fechaza);
                let kc = findkcmes(this.getmes(fechaza));
                let riego = findriegofecha(fechaza);
                let etp = eto * kc;
                let bh = findbhfecha(fechaza);
                let fechabh = fechaza.split('-')[2] + '-' + fechaza.split('-')[1] + '-' + String(parseInt(fechaza.split('-')[0]) - 1)
                let bhanterior = findbhfecha(fechabh);
                let pp = findppfecha(fechaza);
                if (bh == null) {
                    if (bhanterior != null) {
                        let bhfinal = {
                            fecha: fechaza,
                            id_cultivo: this.cultivousado,
                            bh: (bhanterior + pp - etp + riego),
                        };
                        this.postBH(bhfinal);
                    }
                } else {
                    if (bhanterior != null) {
                        let bhfinal = findbhfecha2(fechabh);
                        bhfinal.bh = (bhanterior + pp - etp + riego);
                        this.putBH(bhfinal)
                    }
                }
            },
            async postBH(bh) {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "bh/";
                this.respuesta = await gastoSalienteAPI.postData(bh);
            },
            async putBH(bh) {
                let respuesta = "vacio";
                await axios
                    .put(
                        "http://45.162.168.170:8081/bh/" +
                        bh.id_bh +
                        "/",
                        bh
                    )
                    .then(function (data) {
                    })
                    .catch(function (error) {
                        const mje =
                            error.response.status < 500
                                ? "Los datos no son válidos"
                                : "Se ha detectado un problema ";
                        swal("¡ERROR!", mje, "error");
                        respuesta = error.response.data;
                    });
            },
            findkcmes(mesardo) {
                let kcmes = this.kcs.find((s) => {
                    return (s.mes == mesardo && s.id_cultivo == this.cultivousado);
                })
                if (kcmes == undefined) {
                    return null
                } else {
                    console.log('devuelvo')
                    return kcmes.kc;
                }
            },
            findbhfecha(fecharda) {
                let riegofecha = this.bhs.find((s) => {
                    return (s.fecha == fecharda && s.id_cultivo == this.cultivousado);
                })
                if (riegofecha == undefined) {
                    return null
                } else {
                    return riegofecha.bh;
                }
            },
            findbhfecha2(fecharda) {
                let riegofecha = this.bhs.find((s) => {
                    return (s.fecha == fecharda && s.id_cultivo == this.cultivousado);
                })
                if (riegofecha == undefined) {
                    return null
                } else {
                    return riegofecha;
                }
            },
            findriegofecha(fecharda) {
                let riegofecha = this.riegos.find((s) => {
                    return (s.fecha == fecharda && s.id_cultivo == this.cultivousado);
                })
                if (riegofecha == undefined) {
                    return 0
                } else {
                    return riegofecha.riego;
                }
            },
            findetofecha(fecharda) {
                console.log(this.etos);
                let etofecha = this.etos.find((s) => {
                    return (s.fecha == fecharda && s.id_campo == this.campousado);
                })
                if (etofecha == undefined) {
                    return null
                } else {
                    return etofecha.eto;
                }
            },
            async getKcs() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "kc/";
                this.kcs = await gastoSalienteAPI.getData();
            },
            async getBhs() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "bh/";
                this.bhs = await gastoSalienteAPI.getData();
            },
            async getEtos() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "eto/";
                this.etos = await gastoSalienteAPI.getData();
            },
            async getRiegos() {
                let gastoSalienteAPI = new APIControler();
                gastoSalienteAPI.apiUrl.pathname = "riego/";
                this.riegos = await gastoSalienteAPI.getData();
            },
            showinput() {
                this.ppus = false;
            },
            periodocompletardo() {
                this.periodocomplpp = [];
				this.mesesperiodo()
				this.fechaperiodo()
				this.meses.forEach((mes) =>
				{
					let opcion = {
						mesusado: mes,
						tabla_fechas: [],
						tabla_pp: [],
					};
					this.fechas.forEach((fecha) =>
					{
                        let fechaformat = fecha.split('-')[2] + '-' + fecha.split('-')[1] + '-' + fecha.split('-')[0]
						let fechuca = new Date(fechaformat);
						let mesuco = (fechuca.getMonth() + 1)
						console.log(mesuco)
						let mesito;
                        switch (mesuco) {
                            case 1:
                                mesito = 'Enero'
                                break;
                            case 2:
                                mesito = 'Febrero'
                                break;
                            case 3:
                                mesito = 'Marzo'
                                break;
                            case 4:
                                mesito = 'Abril'
                                break;
                            case 5:
                                mesito = 'Mayo'
                                break;
                            case 6:
                                mesito = 'Junio'
                                break;
                            case 7:
                                mesito = 'Julio'
                                break;
                            case 8:
                                mesito = 'Agosto'
                                break;
                            case 9:
                                mesito = 'Septiembre'
                                break;
                            case 10:
                                mesito = 'Octubre'
                                break;
                            case 11:
                                mesito = 'Noviembre'
                                break;
                            case 12:
                                mesito = 'Diciembre'
                                break;
						}
						if (mesito == mes)
						{
							opcion.tabla_fechas.push(fecha);
						}
					})
					this.periodocomplpp.push(opcion);
				})
				this.testFetch();
            },
            mesesperiodo() {
                this.meses = [];
                let fechaini = new Date(this.fechainiusada)
                let fechafin = new Date(this.fechafinusada)
                while (fechaini.getTime() <= fechafin.getTime()) {
					let mes;
                    let mesuco = fechaini.getMonth() + 1
					switch (mesuco) {
						case 1:
							mes = 'Enero'
							break;
						case 2:
							mes = 'Febrero'
							break;
						case 3:
							mes = 'Marzo'
							break;
						case 4:
							mes = 'Abril'
							break;
						case 5:
							mes = 'Mayo'
							break;
						case 6:
							mes = 'Junio'
							break;
						case 7:
							mes = 'Julio'
							break;
						case 8:
							mes = 'Agosto'
							break;
						case 9:
							mes = 'Septiembre'
							break;
						case 10:
							mes = 'Octubre'
							break;
						case 11:
							mes = 'Noviembre'
							break;
						case 12:
							mes = 'Diciembre'
							break;
					}
					if (!this.meses.includes(mes))
					{
                        this.meses.push(mes)
					}
                    fechaini.setDate(fechaini.getDate() + 1);
                }
                console.log(this.fechas);
			},

			altaPp() {},

			//Elimino un gasto
			async deletePp(id_pp) {
				axios
                    .delete("https://arandadistribucion.pythonanywhere.com/pp/" + id_pp + "/")
					.then((datos) => {
						swal("Operación Exitosa", " ", "success");
						console.log(datos);
						this.hideModal();
					})
					.catch((error) => {
						swal("¡ERROR!", "Se ha detectado un problema ", "error");
						console.log(error);
						this.hideModal();
					})
					.finally(() => this.testFetch());
			},
			llenarfields(fechas)
			{
                console.log('tabla_ventas');
				console.log(fechas);
				let fields = [];
				fechas.forEach((fecha) =>
				{
					console.log('anashey')
					let option = {}
					option.key = fecha
					option.text = fecha
					console.log(option);
					fields.push(option);
				})
				console.log('god')
                console.log(fields)
				return fields;
			},
            redirect() {
                if (!this.$session.exists()) {
                    window.location.replace('/login');
                }
                if (this.$session.get('tipocuenta') == 'lector') {
                    this.dardealta = true;
                }

			},
            async getCultivos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "cultivos/";
                this.cultivos = await productosAPI.getData();
            },
            async getCampos() {
                let productosAPI = new APIControler();
                productosAPI.apiUrl.pathname = "campos/";
                this.listacli = await productosAPI.getData();
                let option
                this.listacli.forEach((element) => {
                    option = {};
                    option.value =
                        "http://45.162.168.170:8081/campos/" + element.id_campo + "/";
                    option.text = element.id_campo + ' - ' + element.nombre + ' - ' + element.calle;
                    this.opcampos.push(option);
                });
			},
			getCampo(id) {
				if (id != null)
				{
                    id = id.split('/')[4]
				}
                if (this.listacli == []) {
                    return getCampos().then(() => {
                        return this.getCampo(id)
                    })
                }
                else {
                    return this.listacli.find(s => {
                        return s.id_campo == id
                    })
                }
            },
			//Elimina todas los gastos
			async delete_all_Pp() {
				var cantidad = this.selected.length;

				try {
					for (var i = 0; i < cantidad; i++) {
						axios.delete(
							"https://arandadistribucion.pythonanywhere.com/pp/" +
								this.selected[i].id_pp +
								"/"
						);
						if (this.selected.length == 0) {
							console.log("Eliminacion Exitosa");
							break;
						}
					}
					this.hideModal();
					swal("Eliminacion Exitosa", " ", "success");
				} catch (error) {
					this.hideModal();
					swal("¡ERROR!", "Se ha detectado un problema ", "error");
					console.log(error);
				} finally {
					this.testFetch();
				}
			},

			//Funcion de busqueda
			onFiltered(filteredItems) {
				// Trigger pagination to update the number of buttons/pages due to filtering
				this.totalRows = filteredItems.length;
				this.currentPage = 1;
			},
			getLotes(campo) {
                let lotes = [];
				if (campo != null)
				{
                    this.cultivos.forEach((cult) => {
                        if (parseInt(cult.id_campo.split('/')[4]) == campo.id_campo) {
                            lotes.push(cult);
                        }
                    })
				}
                return lotes;
            },
			//-----Funciones de seleccion
			//Selecciona una a una
			seleccionar_una(items) {
				this.selected = items;
				if (this.selected.length > 0) {
					this.btn_del_full = false;
					this.btn_limpiar = false;
					if (this.selected.length > 1) {
						this.btn_mostrar = true;
						this.btn_editar = true;
						this.btn_eliminar = true;
					}
					if (this.selected.length == this.rows) {
						this.btn_select = true;
					} else {
						this.btn_select = false;
					}
				} else {
					this.btn_del_full = true;
					this.btn_mostrar = false;
					this.btn_editar = false;
					this.btn_eliminar = false;
					this.btn_limpiar = true;
				}
			},
            fechaactual() {
				const f = new Date();
                f.setDate(f.getDate() + 30);
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
            fechaactual2() {
                const f = new Date();
                const formatDate = (d) => {
                    return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
                }
                console.log(formatDate(f));
                return (formatDate(f));
            },
			inicializar()
			{
				this.fechainiusada = this.fechaactual2();
				this.fechafinusada = this.fechaactual();
				this.periodocompletardo();
                if (this.$session.exists()) {
					this.cultivousado = 'http://45.162.168.170:8081/cultivos/' + String(this.$session.get('cultivused').id_cultivo) + '/';
                    this.campousado = String(this.$session.get('cultivused').id_campo)
                }
			},
			//Selecciona todas
			seleccionar_todas() {
				this.$refs.tablaregistros.selectAllRows();
				this.btn_del_full = false;
				this.btn_mostrar = true;
				this.btn_editar = true;
				this.btn_eliminar = true;

				this.btn_select = true;
				this.btn_limpiar = false;
			},
			//Limpia todas las selecciones
			limpiar_seleccion() {
				this.$refs.tablaregistros.clearSelected();
				this.btn_del_full = true;
				this.btn_mostrar = false;
				this.btn_editar = false;
				this.btn_eliminar = false;

				this.btn_select = false;
				this.btn_limpiar = true;
			},
		},

		beforeMount() {
			this.getPps();
			this.getCultivos();
			this.redirect();
			this.inicializar();
			this.getCampos();
		},
	};
</script>

